import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { BaseView } from '../cee-base-class';
import { UserDataHandlerService } from '../../services/user-data-handler.service';
import { SharedEventsServiceService } from '../../services/shared-events-service.service';
import { AppDataService } from '../../models/app-data/app-data.service';
import { ApiDataService } from '../../models/api-data/api-data.service';
import { WfeStepLoaderService } from '../../services/wfe-step-loader.service';
import { FlatUnflat } from '../../utils/flat-unflat-json';
import { MatDialog as MatDialog } from '@angular/material/dialog';
import { MatSnackBar as MatSnackBar } from '@angular/material/snack-bar';
import { HttpClient } from '@angular/common/http';
import { CEEInternalEmitterService } from '../../services/internal-cee-emitter-service.service';
import { NgxImageZoomModule } from 'ngx-image-zoom';
import { CommonModule, NgClass } from '@angular/common';

@Component({
    selector: 'app-cee-carousel',
    templateUrl: './cee-carousel.component.html',
    styleUrls: ['./cee-carousel.component.scss'],
    standalone: true,
    imports: [NgClass, NgxImageZoomModule, CommonModule]
})
export class CeeCarouselComponent extends BaseView implements OnInit, OnDestroy {
    @Input() declare stepId: any;
    @Input() declare fieldData: any;
    @Input() primaryKeyIndex: any;
    @Input() keepState = false;
    @Input() rowData: any;

    images = [];
    slideIndex = 0;
    showNumbers = false;
    showNextPrevious = false;
    countText = '';
    autoCyclingInterval;
    autoCyclingPause: boolean = false;
    thumbnailCarouselPosition: any = "buttom";
    zoomInEventType: any = "onhover";
    isZoomInModal: any = false;
    private flatUnflat: FlatUnflat;
    constructor(
        public userDataHandlerService: UserDataHandlerService,
        public sharedEventsService: SharedEventsServiceService,
        public internalCEEEmitter: CEEInternalEmitterService,
        public appDataService: AppDataService,
        public apiDataService: ApiDataService,
        public wfeStepLoaderService: WfeStepLoaderService,
        public dialog: MatDialog,
        public snackBar: MatSnackBar,
        public http: HttpClient
    ) {
        super(userDataHandlerService, sharedEventsService,
            internalCEEEmitter,
            appDataService, apiDataService, wfeStepLoaderService, dialog, snackBar, http);
        this.flatUnflat = new FlatUnflat(appDataService);
    }
    ngOnInit() {
        this.init(this.stepId, this.fieldData, this.keepState, this.rowData);
    }

    onViewDataInit(): void {
        // set images when the data is loaded.
        if (this.fieldData.field_style.field_assets && this.fieldData.field_style.field_assets.length > 0 && this.fieldData.possible_values) {
            this.fieldData.field_style.field_assets.map((data, key) => {
                this.images.push(
                    {
                        image: data.path,
                        thumbnail: data.thumbnail,
                        highres: data.highres,
                        text: this.fieldData.possible_values[key] ? this.fieldData.possible_values[key] : ''
                    }
                );
            });
        }
        this.thumbnailCarouselPosition = this.additionalParameter['CEE_Carousel_Position'] && this.additionalParameter['CEE_Carousel_Position'] != "" ? this.additionalParameter['CEE_Carousel_Position'].toLowerCase() : 'buttom';
        this.zoomInEventType = this.additionalParameter['CEE_ZoomInEventType'] && this.additionalParameter['CEE_ZoomInEventType'] != "" && this.additionalParameter['CEE_ZoomInEventType'].toLowerCase() == 'onhover' ? "hover" : 'click';
        this.isZoomInModal = this.additionalParameter['CEE_ZoomInModal'] && this.additionalParameter['CEE_ZoomInModal'] != "" && this.additionalParameter['CEE_ZoomInModal'].toLowerCase() == "true" ? true : false;
        if (this.additionalParameter['AutoCycling'] && this.additionalParameter['AutoCycling'] !== 'false' && parseInt(this.additionalParameter['AutoCycling'])) {
            this.autoCycling(parseInt(this.additionalParameter['AutoCycling']) * 1000);
        }
    }
    onAPICallback(data: any): void {
        const apiData = this.setApiCallBackDataUtil.setApiCallBackData(this.fieldData, data);
        if (apiData !== null) {
            if (Array.isArray(apiData)) {
                this.images = [];
                for (const data of apiData) {
                    if(typeof(data) == 'object') {
                        this.images.push(
                            {
                                image: data.path,
                                thumbnail: data.thumbnail,
                                highres: data.highres,
                            }
                        );
                    } else {
                        this.images.push(
                            {
                                image: data
                            }
                        );
                    }
                    
                }
            }
        }
    }

    concatData(data: any): void {
        // throw new Error("Method not implemented.");
    }
    onEmptySession(data: any): void {
        //throw new Error('Method not implemented.');
    }
    handleCustomFunction(get_data: any) {
        //throw new Error('Method not implemented.');
    }
    ngOnDestroy(): void {
        // throw new Error("Method not implemented.");
        if (this.autoCyclingInterval) {
            clearInterval(this.autoCyclingInterval);
            this.autoCyclingInterval = null;
        }

        // Clear images array and related data
        this.clearImages();

        // Clear utility object references
        this.flatUnflat = null;

        // Reset component state
        this.slideIndex = 0;
        this.showNumbers = false;
        this.showNextPrevious = false;
        this.countText = '';
        this.autoCyclingPause = false;
        this.thumbnailCarouselPosition = null;
        this.zoomInEventType = null;
        this.isZoomInModal = false;

        // Clear input properties

        this.primaryKeyIndex = null;

    }

    private clearImages(): void {
        if (this.images && this.images.length > 0) {
            // Clear the array completely
            this.images.length = 0;
            this.images = [];
            
            // Reset slide index
            this.slideIndex = 0;
        }
    }

    setFieldValueOnSetValuesEvent(data: object) {

    }

    setFieldStateOnStateChangeEvent(data: object) {

    }

    // Next/previous controls
    plusSlides(n) {
        if (this.slideIndex + n === this.images.length) {
            this.slideIndex = 0;
        } else if (this.slideIndex + n < 0) {
            this.slideIndex = this.images.length + n;
        } else {
            this.slideIndex += n;
        }
    }
    // Thumbnail image controls
    currentSlide(n) {
        this.slideIndex = n;
    }

    autoCycling(t: number = 1000) {
        const _this = this;
        this.autoCyclingInterval = setInterval(function () {
            if (!_this.autoCyclingPause) _this.plusSlides(1);
        }, t);
    }

    toggleLanguage(data: object) {

    }

    updateStateHistory(data: any): void {
        // throw new Error("Method not implemented.");
    }

    openPictureInModal(image) {
        let data = {'image': image, 'zoomInEventType': this.zoomInEventType, 'zoomImg': true}
        this.wfeEventListHandler.openAlert(data);
    }
}
